body {
  margin: 0;
  font-family: 'Times New Roman', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input {
  text-align: left;
  line-height: 1.5rem;
  border: 2px solid black;
  padding: 2px;
  margin: 5px;
}

button {
  background-color: black;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border: 2px solid black;
  margin: 5px;
}

.error {
  background-color: black;
  color: lightsalmon;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  margin-bottom: 10px;
}

.success {
  background-color: black;
  color: lightseagreen;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  margin-bottom: 10px;
}

.container {
  display: flex;
  gap: 20px;
}

ul.lib {
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
}

li.book-thumbnail {
  border: 2px solid black;
  margin: 10px;
  padding: 5px;
  text-align: center;
}

.lib-thumbnail h3 {
  font-weight: bold;
  font-style: italic;
}

.lib-thumbnail p {
  font-size: 0.8rem;
  margin-bottom: 5px;
}